<template>
  <el-button :size="size" :type="type" :plain="plain" v-if="status" icon="el-icon-loading"></el-button>
  <el-button :size="size" :type="type" :plain="plain" v-else @click="submit()">
    <slot></slot>
  </el-button>
</template>

<script>
/**
 * 
 * props:
 * status -> 
 * type -> 
 * emit:
 * btn -> 
 */
import { debounce } from "@/util"; // 
export default {
  props: {
    // 
    status: {
      type: Boolean,
      default: false
    },
    // 
    type: {
      type: String,
      default: "primary"
    },
    // 
    plain: {
      type: Boolean,
      default: false
    },
    // 
    size: {
      type: String,
      default: "medium"
    }
  },
  methods: {
    // 
    submit: debounce(function () {
      this.$emit("btn");
    }, 2000, true)
  }
};
</script>
