<template>
  <div>
    <div class="upload-box">
      <!--  -->
      <slot></slot>
      <!--  :disabled="disUpload"-->
      <el-upload class="upload-demo" ref="upload" drag multiple with-credentials :action="url" :limit="limit"
        :data="upOptions" :headers="headers" :file-list="fileList" :auto-upload="autoUpload" @change="beforeUpload"
        :on-error="handleError" :on-exceed="handleExceed" :before-upload="beforeUpload" :on-success="handleSuccess">
        <el-icon class="el-icon--upload">
          <i-upload-filled />
        </el-icon>
        <div class="el-upload__text">
          
          <em></em>
        </div>
      </el-upload>
    </div>
  </div>
</template>

<script>
/**
 * auth:weilan
 * github: https://github.com/hql7
 * props: 
 * title:
 * templateCode:[String, Array],array
 * url:
 * options:
 * autoUpload:
 * uploadSuccess:
 * reg:excel
 * path:
 * contract: 
 * defaultProps
 * view: 
 * iscom: 
 * schedule-plan: 
 * emit: 
 * uploadSuccess
 * length: 
 * solt: 
 *  templateMsg
 * --------> path
 * --------> 
 * --------> refthis.$refs.[""].toUpload()!
 * --------> v-ifclaim-apply.vue
 */

export default {
  data() {
    return {
      fileList: [], // 
    };
  },
  props: {
    // 
    headers: Object,
    // 
    url: {
      type: String,
      default: "" // 
    },
    // 
    options: Object,
    // 
    autoUpload: {
      type: Boolean,
      default: false
    },
    // 
    reg: {
      type: Boolean,
      default: true
    },
    // 
    regFuc: Function,
    // 
    limit: Number,
  },
  methods: {
    // 
    toUpload() {
      this.$refs.upload.submit();
    },
    // 
    beforeUpload(file, fileList) {
      this.$emit('beforeUpload', file, fileList );
      // 
      if (!this.reg) return true;
      // 
      if (this.regFuc) return this.regFuc(file);
    },
    // 
    handleSuccess(res, file, fileList) {
      this.$emit("uploadSuccess", res, file, fileList);
    },
    // 
    handleError(err) {
      this.$emit("uploadError", err);
    },
    // 
    handleExceed() {
      this.$message.error("1");
    },
  },
  computed: {
    // 
    upOptions() {
      return this.options;
    }
  },
};
</script>

<style lang="scss">
.upload-box {

  .el-upload,
  .el-upload-dragger {
    width: 100%;
    height: 140px;
    padding: 10px 10px;

  }
}
</style>
