<template>
  <!--  authweilan time2019-10-25 githubhttps://github.com/hql7 -->
  <div class="wl-explorer" @contextmenu.prevent>
    <!--  -->
    <el-form class="wl-header-btn" :inline="true" :size="size" @submit.native.prevent>
      <el-form-item>
        <!-- <el-button type="primary" @click="handleFolder('add')"></el-button> -->
        <!-- <el-button :disabled="disabledEditFolder" @click="handleFolder('edit')"></el-button> -->
        <el-button @click="showUpload"></el-button>
        <submit-btn type="danger" :size="size" @btn="handleDel" :status="load.del"></submit-btn>
        <!-- solt -->
        <slot name="header-btn"></slot>
      </el-form-item>
      <el-form-item v-if="selfHeaderDropdown.length > 0">
        <el-dropdown trigger="click" placement="bottom" @command="handleDropdown">
          <el-button type="primary" plain>
            
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <template #dropdown>
            <!-- props -->
            <el-dropdown-item v-for="i of selfHeaderDropdown" :key="i.id" :icon="i.icon" :command="i.command"
              :divided="i.divided" :disabled="i.disabled">{{ i.name }}</el-dropdown-item>
            <!-- solt -->
            <slot name="header-dropdown"></slot>
          </template>
        </el-dropdown>
      </el-form-item>
      <el-form-item v-show="uploading.ing">
        <span></span>
        <span class="c-blue u-uploading-name">{{ uploading.name }}</span>
        <span class="c-blue">({{ uploading.percentage }}%)</span>
      </el-form-item>
      <el-form-item class="u-right">
        <i class="iconfont icon-wl-list file-show-type" v-show="layout.show_list"
          @click="layout.show_list = !layout.show_list"></i>
        <i class="iconfont icon-wl-grid file-show-type" v-show="!layout.show_list"
          @click="layout.show_list = !layout.show_list"></i>
      </el-form-item>
    </el-form>
    <!---->
    <el-form :inline="true" :size="size" label-position="le" :model="file" class="wl-header-file"
      @submit.native.prevent>
      <!-- <el-form-item class="file-path-box">
        <div class="file-path-text" :class="{ small: size == 'default' }" v-show="!layout.edit_path"
          @click="handleFilePath">
          <img class="file-path-img" src="./images/folder@3x.png" alt="" title="" />
          {{ file.path }}
        </div>
        <el-autocomplete class="u-full" :size="size" ref="file-path-ipt" placeholder="" v-model="file.path"
          v-if="layout.edit_path" @keyup.enter.native="filePathChange" @select="filePathChange"
          :fetch-suggestions="pathQuerySearch">
          <template #default="{ item }">
            <div style="display: flex;align-items: center">
              <img class="file-path-img" src="./images/folder@3x.png" alt="" title="" />
              <div class="value">{{ item.value }}</div>
            </div>
          </template>
        </el-autocomplete>
      </el-form-item> -->
      <el-form-item class="file-search-box" label=":">
        <el-select v-model="file.fileType" @change="fileSearch()">
          <el-option v-for="item in selectOptions" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </el-form-item>
      <el-form-item class="file-search-box">
        <el-input v-model="file.key" placeholder="" :size="size" @keyup.enter.native="fileSearch()">
          <template #append>
            <el-button :size="size" type="primary" @click="fileSearch()"></el-button>
          </template>
        </el-input>
      </el-form-item>
      <el-form-item class="file-handle-box">
        <i class="iconfont icon-wl-left file-path-handle" :class="{ 'u-disabled': pathIsStart }"
          @click="pathBtn('prv')"></i>
        <i class="iconfont icon-wl-right file-path-handle" :class="{ 'u-disabled': pathIsEnd }"
          @click="pathBtn('next')"></i>
        <i class="iconfont icon-wl-up file-path-handle" :class="{ 'u-disabled': path.level === 1 }"
          @click="pathBtn('top')"></i>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-scrollbar class="wl-main-scroll">
      <!--  -->
      <div class="wl-main-list">
        <!--  -->
        <el-table v-show="layout.show_list" @selection-change="filrChecked" highlight-current-row :border="showBorder"
          :data="self_data" class="wl-table" ref="wl-table">
          <el-table-column v-if="showCheckbox" align="center" type="selection" min-width="35"></el-table-column>
          <el-table-column v-if="showIndex" align="center" type="index" label="" min-width="35"></el-table-column>
          <slot name="table-column-top"></slot>
          <el-table-column v-for="i of selfColumns" show-overflow-tooltip :key="i._id" :prop="i.prop" :width="i.width"
            :label="i.label" :fixed="i.fixed" :align="i.align" :sort-by="i.sortBy" :sortable="i.sortable"
            :min-width="i.minWidth" :formatter="i.formatter" :column-key="i.columnKey" :class-name="i.className"
            :sort-method="i.sortMethod" :header-align="i.headerAlign" :render-header="i.renderHeader"
            :label-class-name="i.labelClassName">
            <template #default="scope">
              <!--  -->
              <template v-if="i.prop !== selfProps.name">
                {{
                    i.formatter
                      ? i.formatter(
                        scope.row,
                        scope.column,
                        scope.row[i.prop],
                        scope.$index
                      )
                      : scope.row[i.prop]
                }}
              </template>
              <!--  -->
              <div v-else @click="enterTheLower(scope.row, scope.row[selfIsFolder])" class="wl-name-col wl-is-folder">
                <!--  -->
                <div class="namecol-iconbox">
                  <img :src="fileTypeIcon(scope.row)" class="name-col-icon" alt="" />
                </div>
                <!--  -->
                <div class="namecol-textbox">
                  {{
                      i.formatter
                        ? i.formatter(
                          scope.row,
                          scope.column,
                          scope.row[i.prop],
                          scope.$index
                        )
                        : scope.row[i.prop]
                  }}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column align="center" min-width="180" label="">
            <template #default="scope">
              <el-button type="primary" size="small" @click="emitParent(item.value, { e: '', data: scope.row })"
                v-for="(item, index) in selfProps.menuList" :key="index">
                {{ item.label }}</el-button>
            </template>
          </el-table-column>
          <slot name="table-column-bottom"></slot>
        </el-table>
        <!--  -->
        <ul class="wl-list" v-show="!layout.show_list">
          <li class="wl-list-item wl-is-folder" @contextmenu="onContextMenu($event, i)" v-for="(i, idx) in self_data"
            :key="i.Id">
            <el-checkbox class="wl-checkbox" :class="{ 'wl-checkbox-checked': i._checked }"
              @change="listItemCheck($event, i)" v-model="i._checked"></el-checkbox>
            <div @click="enterTheLower(i, i[selfIsFolder])">
              <img :src="fileTypeIcon(i)" class="name-col-icon" alt="" />
              <p class="list-item-name" :title="i[selfProps.name]">
                {{
                    i.formatter
                      ? i.formatter(i, null, i[selfProps.name], idx)
                      : i[selfProps.name]
                }}
              </p>
            </div>
          </li>
        </ul>
        <!--  -->
        <slot name="main"></slot>
      </div>
    </el-scrollbar>
    <!-- slot dom -->
    <slot></slot>
    <!--  -->
    <template v-if="usePreview">
      <el-dialog v-model="layout.view" title="" width="70%" draggable>
        <file-view v-show="layout.view" v-if="layout.view" :key="previewKey" ref="file-view"
          class="file-view-components" :previewType="previewType" :previewOptions="previewOptions"
          @close="layout.view = false"></file-view>
        <template #footer>
          <span class="dialog-footer">
            <el-button type="primary" @click="layout.view = false"></el-button>
          </span>
        </template>
      </el-dialog>
    </template>
    <!--  -->
    <el-dialog title="" width="520px" v-model="layout.move">
      <el-tree-select v-model="move_selected" :data="tree_path" :props="selfMoveProps" :nodeKey="selfProps.pathId"
        :size="size" :render-after-expand="false" />
      <el-divider />
      <template #footer class="dialog-footer">
        <el-button :size="size" @click="layout.move = false"> </el-button>
        <submit-btn :size="size" @btn="fileMove" :status="load.move"> </submit-btn>
      </template>
    </el-dialog>
    <!--  -->
    <template v-if="useUpload">
      <fade-in v-show="layout.upload">
        <h3 class="edit-header"></h3>
        <el-scrollbar class="scroll">
          <el-form :size="size" ref="template_form" label-position="top" class="template_form rule-form">
            <el-form-item label="">
              <wlTreeSelect class="u-full" :size="size" :data="tree_path" :props="selfMoveProps"
                :nodeKey="selfProps.pathId" v-model="upload_selected" @change="uploadPathChange"></wlTreeSelect>
            </el-form-item>
            <el-form-item label="">
              <uploadItem ref="upload-item" :size="size" :reg="uploadReg" :url="uploadUrl" :limit="uploadLimit"
                :regFuc="uploadRegFuc" :options="uploadOptions" :headers="uploadHeaders" @beforeUpload="uploadBefore"
                @uploadSuccess="uploadSuccess" @uploadError="uploadError"></uploadItem>
            </el-form-item>
          </el-form>
        </el-scrollbar>
        <!--  -->
        <div class="submit-btn-box">
          <submit-btn :size="size" @btn="saveUpload()" :status="load.upload"></submit-btn>
          <el-button :size="size" @click="layout.upload = false"></el-button>
        </div>
      </fade-in>
    </template>
  </div>
</template>

<script>
import submitBtn from "@/components/submit-btn.vue"; // 
import fileView from "@/components/file-view.vue"; // 
import fadeIn from "@/components/fade-in.vue"; // 
import uploadItem from "@/components/upload-item"; // 
import { arrayToTree, splicParentsUntil, download } from "@/util"; // 
import { ElMessageBox, ElMessage } from "element-plus";
const guid = "00000000-0000-0000-0000-000000000000";
export default {
  name: "wlExplorer",
  components: { submitBtn, fileView, fadeIn, uploadItem },
  data() {
    return {
      load: {
        del: false, // 
        move: false, // 
        upload: false, // 
      }, // loading
      uploading: {
        name: "",
        percentage: 0,
        ing: false,
      }, // 
      layout: {
        show_list: true, // 
        edit_path: false, // 
        view: false, // 
        move: false, // 
        upload: false, // 
      }, // 
      file: {
        pid: "", // 
        id: "", // id
        path: "", // 
        key: "", // ,
        fileType: undefined, //
      }, // 
      path: {
        level: 1, // 
        index: -1, // 
        history: [
          {
            path: "", // 
            pid: "", // 
            id: "", // id
            data: [], // 
          },
        ], // 
      }, // 
      self_data: [], // 
      file_checked_data: [], // 
      matched_path: false, // 
      tree_path: [], // 
      move_selected: "", // 
      upload_selected: "", // 
      uoload_data: {
        pathId: null,
        parentPathId: null,
        isCurrentFolder: true,
      }, // 
      previewKey: ""
    };
  },
  props: {
    /**
     * 
     * 
     * name: String 
     * command: Function 
     * disabled: Boolean 
     * divided: Boolean 
     * icon: String 
     */
    headerDropdown: Array,
    // 
    showCheckbox: {
      type: Boolean,
      default: true,
    },
    // 
    showIndex: {
      type: Boolean,
      default: true,
    },
    // 
    showBorder: {
      type: Boolean,
      default: true,
    },
    // 
    data: Array,
    // [el-Table-column Attributes] (https://element.eleme.cn/#/zh-CN/component/table)
    columns: Array,
    /**
     * 
     * isFolder: Boolean
     * name: String 
     */
    props: Object,
    //  ,
    allPath: Array,
    // rowisFolderFnpropsisFolder
    isFolderFn: Function,
    // ,true
    isLockFn: Function,
    // 
    useUpload: {
      type: Boolean,
      default: true,
    },
    // 
    uploadUrl: {
      type: String,
      default: "",
    },
    //  
    uploadReg: {
      type: Boolean,
      default: false,
    },
    // Boolean
    uploadRegFuc: Function,
    // 
    uploadHeaders: Object,
    // 
    uploadOptions: Object,
    // 
    uploadLimit: Number,
    // 
    usePreview: {
      type: Boolean,
      default: true,
    },
    // 
    previewType: {
      type: String,
      default: "img",
    },
    // 
    previewOptions: Object,
    // 
    splicOptions: Object,
    size: {
      type: String,
      default: "default",
    },
    menuList: {
      type: Array,
      default: []
    },
    selectOptions: {
      type: Array,
      default: []
    }
  },
  methods: {
    /**
     * 
     * type: string add edit
     * auth: boolean 
     */
    handleFolder(type) {
      let [_act = null] = this.file_checked_data;
      if (type === "edit" && (!_act || !_act[this.selfIsFolder])) {
        ElMessage({
          showClose: true,
          message: "",
          type: "error",
        });
        return;
      }
      //   
      this.$emit("handleFolder", _act, type, this.file);
      this.closeUpload();
    },
    // 
    handleDel() {
      if (this.file_checked_data.length === 0) {
        ElMessage({
          showClose: true,
          message: "",
          type: "error",
        });
        return;
      }
      // 
      ElMessageBox.alert("", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        showCancelButton: true,
      })
        .then(() => {
          const deleteData = { data: { _id: this.file_checked_data.map(v => v._id) } };
          this.$emit("deleteItem", deleteData);
        })
        .catch(() => {
          ElMessage({
            type: "info",
            message: "",
          });
        });
    },
    // 
    handleDropdown(val) {
      if (typeof val === "function") {
        val(this.file);
        return;
      }
      if (val === "wl-move") {
        this.showMoveList();
      } else if (val === "wl-download") {
        this.wlDownload();
      }
    },
    // 
    handleFilePath() {
      this.layout.edit_path = true;
      this.$nextTick(() => {
        this.$refs["file-path-ipt"].focus();
      });
    },
    // 
    filePathChange(item) {
      if (!this.matched_path) {
        ElMessageBox.alert(
          `"${this.file.path}"`,
          "",
          {
            confirmButtonText: "",
            callback: () => { },
            closeOnClickModal: true,
            closeOnPressEscape: true,
            type: "error",
          }
        );
        return;
      }
      let _act_item = this.path.history.find(
        (i) => i.id === item[this.selfProps.pathId]
      );
      if (_act_item) {
        this.routerActive(_act_item, _act_item.data);
      } else {
        this.routerPush({
          id: item[this.selfProps.pathId],
          pid: item[this.selfProps.pathPid],
          path: item[this.selfProps.pathName],
        });
      }

      this.$emit("search", this.file, !_act_item);
      this.layout.edit_path = false;
    },
    // 
    fileSearch() {
      this.$emit("search", this.file, true);
      // let _act_item = this.path.history.find((i) => i.id === this.file.id);
      // _act_item
      //   ? this.routerActive(_act_item, _act_item.data)
      //   : this.$emit("search", this.file, true);
    },
    /**
     * 
     * file: Object {id: id, pid: id, path: }
     * data: Array 
     */
    routerPush(file, data = []) {
      splicParentsUntil(this.allPath, file, this.selfProps);
      this.clearSearchKey();
      this.path.history.push({
        ...file,
        data,
      });
      this.self_data = data;
      this.file.pid = file.pid;
      this.file.id = file.id;
      this.file.path = splicParentsUntil(this.allPath, file, this.selfProps);
      this.path.level = !file.id || file.id === guid ? 1 : 2;
      this.path.index = -1; // 
    },
    /**
     * 
     * file: Object {id: id, pid: id, path: }
     * data: Array 
     */
    routerActive(file, data) {
      this.clearSearchKey();
      this.file.pid = file.pid;
      this.file.id = file.id;
      this.file.path = splicParentsUntil(this.allPath, file, this.selfProps);
      this.self_data = data;
      this.path.level = !file.id || file.id === guid ? 1 : 2;
    },
    /**
     * 
     * id id
     * data 
     */
    updateHistoryData(id, data) {
      let _target = this.path.history.find((i) => i.id === id);
      if (!_target) return;
      _target.data = data;
    },
    // 
    showMoveList() {
      if (this.file_checked_data.length === 0) {
        ElMessage({
          showClose: true,
          message: "",
          type: "error",
        });
        return;
      }
      this.layout.move = true;
    },
    // 
    wlDownload() {
      if (this.file_checked_data.length === 0) {
        ElMessage({
          showClose: true,
          message: "",
          type: "error",
        });
        return;
      }
      this.$emit("download", this.file_checked_data, download);
    },
    // 
    pathBtn(type) {
      if (type === "prv") {
        if (this.pathIsStart) return;
        if (this.path.index === -1) {
          this.path.index = this.path.history.length - 1;
        }
        this.path.index -= 1;
        let _prv = this.path.history[this.path.index];
        this.routerActive(_prv, _prv.data);
      } else if (type === "next") {
        if (this.pathIsEnd) return;
        this.path.index += 1;
        let _next = this.path.history[this.path.index];
        this.routerActive(_next, _next.data);
      } else {
        if (this.path.level === 1) return;
        let _pid = this.file.pid !== guid ? this.file.pid : "";
        let _parent_history = this.path.history.find((i) => i.id === _pid);
        if (_parent_history) {
          this.path.history.splice(
            this.path.history.findIndex((i) => i.id === _pid),
            1
          );
          this.routerPush(_parent_history, _parent_history.data);
          return;
        }
        //  
        let _parent = this.selfPathHistory.find((i) => i.id === _pid);
        if (!_parent) return;
        this.routerPush({
          id: _parent[this.selfProps.pathId],
          pid: _parent[this.selfProps.pathPid],
          path: _parent[this.selfProps.pathName],
        });

        this.$emit("search", this.file, true);
      }
    },
    /**
     * -
     * row: Object 
     * isFolder: Boolean 
     */
    enterTheLower(row, isFolder) {
      if (!isFolder) {
        this.previewFile(row);
        return;
      }
      let _children = this.path.history.find(
        (i) => i.id === row[this.selfProps.pathId]
      );
      if (_children) {
        // 
        this.path.history.splice(
          this.path.history.findIndex(
            (i) => i.id === row[this.selfProps.pathId]
          ),
          1
        );
        this.routerPush(_children, _children.data);
        return;
      }
      //  
      this.routerPush({
        id: row[this.selfProps.pathId],
        pid: row[this.selfProps.pathPid],
        path: row[this.selfProps.pathName],
      });
      this.$emit("search", this.file, true);
    },
    // 
    fileMove() {
      this.$emit(
        "move",
        this.move_selected[0],
        this.file_checked_data,
        this.load.move
      );
      this.$nextTick(() => {
        this.layout.move = false;
      });
    },
    // 
    showUpload() {
      this.upload_selected = this.file.id;
      this.uoload_data = {
        parentPathId: this.file.pid,
        pathId: this.file.id,
        isCurrentFolder: true,
      };
      if (this.useUpload) {
        this.layout.upload = true;
        this.$emit("closeFade");
      } else {
        this.$emit("showUpload");
      }
    },
    // 
    closeUpload() {
      this.layout.upload = false;
    },
    // 
    uploadPathChange([val]) {
      const pathId = val[this.selfProps.pathId];
      this.uoload_data = {
        parentPathId: val[this.selfProps.pathPid],
        pathId,
        isCurrentFolder: pathId == this.file.id,
      };
    },
    // 
    saveUpload() {
      this.$emit("upload", this.uoload_data, this.handleUpload);
    },
    // 
    handleUpload() {
      this.$refs["upload-item"].toUpload();
    },
    // 
    uploadSuccess(res) {
      this.$emit("uploadSuccess", res);
      this.closeUpload();
      if (!res.Data) return;
      let _res_data = res.Data || {};
      if (this.isFolderFn) {
        _res_data.isFolder = this.isFolderFn(_res_data);
      }
      if (this.isLockFn) {
        _res_data.isLock = this.isLockFn(_res_data);
      }
      if (this.explorer_upload_data.bizId === this.file.id) {
        this.self_data.push(_res_data); //    
        return;
      }
      //   
      let _act = this.path.history.find(
        (i) => i.id === this.explorer_upload_data.bizId
      );
      if (!_act) return;
      _act.data.push(_res_data);
    },
    // 
    uploadBefore(file, fileList) {
      this.$emit("uploadBefore", file, fileList, this.file);
    },
    // 
    uploadError(err) {
      this.$emit("uploadError", err);
      this.load.upload = false;
    },
    // 
    pathQuerySearch(queryString, cb) {
      let restaurants = this.selfPathHistory;
      let results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      this.matched_path = results.length > 0;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // 
    fileTypeIcon(row) {
      let _path = "";
      // 
      if (row[this.selfIsFolder]) {
        _path = row[this.selfIsLock]
          ? require("./images/file_automatic@3x.png")
          : require("./images/folder@3x.png");
        return _path;
      }
      // 
      let _suffix = row[this.selfProps.suffix];
      if (!_suffix) {
        _path = require("./images/file_none@3x.png");
        return _path;
      }
      if (["jpg", "jpeg", "png", "gif", "bmp", "IMAGE"].includes(_suffix)) {
        // 
        _path = require("./images/file_img@3x.png");
      } else if (["zip", "rar", "7z", "ZIP"].includes(_suffix)) {
        _path = require("./images/file_zip@3x.png");
      } else if (
        ["avi", "mp4", "rmvb", "flv", "mov", "m2v", "mkv", "VIDEO"].includes(_suffix)
      ) {
        _path = require("./images/file_video@3x.png");
      } else if (["mp3", "wav", "wmv", "wma", "AUDIO"].includes(_suffix)) {
        _path = require("./images/file_mp3@3x.png");
      } else if (["xls", "xlsx", "EXCEL"].includes(_suffix)) {
        _path = require("./images/file_excel@3x.png");
      } else if (["doc", "docx", "DOC"].includes(_suffix)) {
        _path = require("./images/file_docx@3x.png");
      } else if ("pdf" == _suffix || 'PDF' == _suffix) {
        _path = require("./images/file_pdf@3x.png");
      } else if ("ppt" == _suffix || 'PPT' == _suffix) {
        _path = require("./images/file_ppt@3x.png");
      } else if ("txt" == _suffix || 'TXT' == _suffix) {
        _path = require("./images/file_txt@3x.png");
      } else {
        _path = require("./images/file_none@3x.png");
      }
      return _path;
    },
    // 
    filrChecked(val) {
      this.self_data.forEach((i) => (i._checked = false));
      val.forEach((i) => (i._checked = true));
      this.file_checked_data = val;
    },
    // 
    listItemCheck(check, val) {
      this.$refs["wl-table"].toggleRowSelection(val);
    },
    // 
    clearSearchKey() {
      this.file.key = "";
    },
    // 
    previewFile(row) {
      this.$emit("preview", row, this.showPreview);
    },
    // 
    showPreview() {
      // this.previewKey = new Date().getTime();
      this.layout.view = true;
    },
    // 
    handleDataChange(val) {
      let _data = val || [];
      if (this.isFolderFn) {
        _data.forEach((i) => {
          i.isFolder = this.isFolderFn(i);
        });
      }
      if (this.isLockFn) {
        _data.forEach((i) => {
          i.isLock = this.isLockFn(i);
        });
      }
      if (this.file.key) {
        this.self_data = _data;
        return;
      }
      let _act = this.path.history.find((i) => i.id === this.file.id);
      if (!_act) return;
      _act.data = _data;
      this.routerActive(_act, _data);
    },
    emitParent(event, { e, data }) {
      this.$emit(event, { e, data })
    },
    onContextMenu(e, data) {
      e.preventDefault();
      if (this.selfProps.menuList.length > 0) {
        this.selfProps.menuList.map(v => {
          v.onClick = (event) => {
            console.log(data);
            this.$emit(v.value, { e, data })
          }
        })

        this.filrChecked([data])
        //shou our menu
        this.$contextmenu({
          x: e.x,
          y: e.y,
          items: this.selfProps.menuList
        });
      }
    }
  },
  computed: {
    // 
    selfHeaderDropdown() {
      let _data = this.headerDropdown || [];
      _data.forEach((i, idex) => {
        i.id = i.id || `h-drop-id-${idex}`;
      });
      return _data;
    },
    // 
    selfColumns() {
      let _data = this.columns || [];
      _data.forEach((i, idx) => {
        i._id = `_col_${idx}`;
      });
      return _data;
    },
    // 
    selfProps() {
      return {
        isFolder: "isFolder", // Boolean ,isFolderFn
        isLock: "isLock", // Boolean ,isLockFn
        name: "name", // String 
        suffix: "suffix", // String 
        match: "name", // String 
        splic: true, // Boolean match
        pathName: "name", // String  
        pathId: "id", // String  id
        pathPid: "pid", // String  pid
        pathChildren: "children", // String  children
        pathDisabled: "disabled", // String  
        pathConnector: "\\", // String ,'\'
        pathParents: "parents", // String identityId
        pathIdentityId: "identityId", // String id
        ...this.props,
      };
    },
    //   
    selfMoveProps() {
      return {
        label: this.selfProps.pathName,
        children: this.selfProps.pathChildren,
        disabled: this.selfProps.pathDisabled,
      };
    },
    // 
    selfIsFolder() {
      return this.isFolderFn ? "isFolder" : this.selfProps.isFolder;
    },
    // 
    selfIsLock() {
      return this.isLockFn ? "isLock" : this.selfProps.isLock;
    },
    // 
    pathIsEnd() {
      return (
        this.path.history[this.path.history.length - 1].id === this.file.id
      );
    },
    // 
    pathIsStart() {
      return this.path.history[0].id === this.file.id;
    },
    // 
    selfPathHistory() {
      let _all_path = this.allPath || [];
      if (this.selfProps.splic) {
        this.allPath.forEach((i) => {
          i.id = i[this.selfProps.pathId];
          i.value = splicParentsUntil(_all_path, i, this.selfProps);
        });
      } else {
        this.allPath.forEach((i) => {
          i.value = i[this.selfProps.match];
        });
      }
      return this.allPath || this.path.history || [];
    },
    // 
    disabledEditFolder() {
      return (
        this.file_checked_data.length !== 1 ||
        !this.file_checked_data[0][this.selfIsFolder]
      );
    },
  },
  watch: {
    // data
    data(val) {
      this.handleDataChange(val);
    },
    // 
    allPath(val) {
      let options = {
        id: this.selfProps.pathId,
        pid: this.selfProps.pathPid,
        children: this.selfProps.pathChildren,
      };
      this.tree_path = arrayToTree(val || [], options);
    },
  },
  created() {
    if (this.data && this.data.length > 0) {
      this.handleDataChange(this.data);
    }
  },
};
</script>

